









































































































































































































































import server from '../../../store/server.js';
export default {
	data() {
		return {
			pageId:'',
			Token: '',
			SubcontractingUnit: '',
			Title: '',
			Number: '',
			RectPeople: '',
			Company: '',
			Building: '',
			Floor: '',
			SafetyDanger: '',
			DangerDes: '',
			ExpirDate: '',
			CrashLevel: '',
			CheckDate: '',
			ReCheckDate: '',
			State: '',
			Rectification: '',
			AddPeople: '',
			Project: '',
			isReply: false, //回复
			isApproval: false, //审批通过
			isRejected: false, //审批拒绝
			isRecheck: false, //复查
			isFinished: false, //完成
			replyContent: '', //回复内容
			rejuectContent: '' ,//拒绝理由
			imageList:[],
			repleyList:[],
			ReState:0,
			RefuseReason:''
		}
	},
	onLoad(option) {
		this.pageId=option.id
		this.Token = server.net.GetSessionToken();
		this.getDetails(option.id);
		this.GetImage();
		this.GetReply();
	},
	onShow(){
		this.GetImage();
		this.GetReply();
	},
	methods: {
		getDetails(id) {
			let url = server.net.setUrl('api/QualityApi/GetDetail');
			let data = {
				qid:id
			}

			uni.request({
				url: url,
				method: 'POST',
				dataType: 'json',
				header: {
					Token: this.Token,
					"content-type": "application/json"
				},
				data: JSON.stringify(data),
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					let jsondata = result.data;
					console.log(res)
					if (result.status == 1) {
						//遍历JSON数组，获取数据对象
						
						this.Number = jsondata.Number;
						this.Company = jsondata.BranchCompanyName;
						this.Project = jsondata.ProjectName;
						this.Title = jsondata.Title;
						this.Floor = jsondata.Floor;
						this.Building = jsondata.Building;
						this.SubcontractingUnit = jsondata.AdditionalUnitName;
						this.SafetyDanger = jsondata.DangerDes;
						this.DangerDes = jsondata.QualityDanger;
						this.RectPeople = jsondata.RectPeopleName
						this.Rectification = jsondata.Rectification;
						this.AddPeople = jsondata.CheckPeopleName;
						this.ExpirDate = jsondata.ExpireDate.substring(0, 10);
						this.CheckDate = jsondata.Checkdate.substring(0, 10);
						this.ReCheckDate = jsondata.ReCheckdate.substring(0, 10);
						this.CrashLevel = jsondata.CrashLevelName;
						this.State = jsondata.StateName;
						this.ReState=jsondata.State;
						this.RefuseReason = jsondata.RefuseReason;
						console.log(jsondata.State)
						switch (jsondata.State) {
							case 0:
								this.isApproval = true;
								this.isRejected = true;
								break;
							case 1:
							
								this.isReply = true;
								break;
							case 2:
							console.log(this.isRejected)
								this.JudgeAc();
								break;
							case 3:
								
								break;
						}

					} else {
						console.log(result.message);
						uni.showToast({
							title: result.message
						})
					}

				},
				fail: (code) => {
					console.log(code)
					uni.showToast({
						title: '数据获取失败，请检查您的网络环境'
					});
				},

			});
		},
		JudgeAc(){
			let url = server.net.setUrl('api/QualityApi/JudgeAc?proid='+this.pageId);
			uni.request({
				url: url,
				method: 'POST', 
				header: {
					token: this.Token,
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					//console.log(res)
					if(res.data=='9'){
						this.isRecheck = true;
						this.isFinished = true;
					}
				},
				fail: () => {},
				complete: () => {}
			});
		},
		Complete(){
			let url = server.net.setUrl('api/QualityApi/Complete');
			let ajaxdata = {
				id: this.pageId
			}
			uni.request({
				url: url,
				method: 'POST', 
				data: JSON.stringify(ajaxdata),
				header: {
					token: this.Token,
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					//console.log(res)
					uni.showToast({
						title: result.message
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 2000)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		Reply(type) { //质量隐患回复  回复为1 复查为2
		let SubType = 0;
		
			switch(type){
				case 1:
					SubType = 1;
				break;
				case 2:
					SubType = 2;
				break;
				default:
				break;
			}
		
			uni.navigateTo({
				url: "Repley?Type=2" + "&SubType=" +SubType+"&id="+this.pageId
			})
			
		}, 
		Review(state) {   //质量隐患审批
			console.log(state);
			let url = server.net.setUrl('api/QualityApi/Pass');
			let ajaxdata = {
				id: this.pageId
			}
			uni.request({
				url: url,
				method: 'POST', 
				data: JSON.stringify(ajaxdata),
				header: {
					token: this.Token,
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					//console.log(res)
					uni.showToast({
						title: result.message
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 2000)
				},
				fail: () => {},
				complete: () => {}
			});
		},Reject(state) {   //质量隐患拒绝审批
			console.log(state);
			let url = server.net.setUrl('api/QualityApi/Refuse');
			let ajaxdata = {
				id: this.pageId,
				reason:this.replyContent
			}
			uni.request({
				url: url,
				method: 'POST',
				data: JSON.stringify(ajaxdata),
				header: {
					token: this.Token,
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					console.log(res)
					uni.showToast({
						title: result.message
					});
					setTimeout(function() {
						uni.navigateBack({
							delta: 1
						})
					}, 2000)
				},
				fail: () => {},
				complete: () => {}
			});
		},
		previewImage: function (e) {
			var current = e.target.dataset.src
			uni.previewImage({
				current: current,
				urls: this.imageList
			})
		},
		GetImage:function(e){
			//获取单据图片
			console.log(this.pageId)
			let url = server.net.setUrl('api/FileApi/GetFileTable?'+'foreignId='+this.pageId+'&type=2');
			uni.request({
				url: url,
				method: 'POST',
				header: {
					token: this.Token,
				},
				success: res => {
					server.net.showWrongTipsEx(res);
					let result = res.data;
					let jsondata = result.data;
					let tempImage = [];
					for(let im of jsondata){
						let imsrc = server.net.setUrl(im.FileSrc);
						tempImage.push(imsrc)
					}
					this.imageList = tempImage;
				},
				fail: () => {},
				complete: () => {}
			});
		},
		GetReply:function(e){
			//获取回复列表
			console.log(this.pageId)
			let url = server.net.setUrl('api/QualityApi/GetRepleyList');
			let data={
				qualityid:this.pageId
			}
			uni.request({
				url: url,
				method: 'POST',
				header: {
					token: this.Token,
				},
				data:JSON.stringify(data),
				success: res => {
					server.net.showWrongTipsEx(res);
					console.log(res);
					let result = res.data;
					let jsondata = result.data;
					for(let it of jsondata){
						for(let im of it.FileImage){
							im.FileSrc = server.net.setUrl(im.FileSrc);
						}
					}
					this.repleyList = jsondata;
				},
				fail: () => {},
				complete: () => {}
			});
		}
	}
}



